import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	withStyles,
	Button,
	Card,
	CardContent,
	Checkbox,
	Divider,
	FormControl,
	FormControlLabel,
	TextField,
	Typography,
	Grid,
	Hidden,
	IconButton
} from '@material-ui/core';
import { darken } from '@material-ui/core/styles/colorManipulator';
import { Link } from 'react-router-dom';
import InputAdornment from '@material-ui/core/InputAdornment';
import { AccountCircle, Visibility, VisibilityOff } from '@material-ui/icons';
import { userActions } from '../../auth/store/actions/user.actions';

import { styles } from '../index';

class AuthManagePassword extends Component {
	state = {
		email: '',
		password: '',
		passwordConfirm: '',
		showPassword: false,
		showPasswordConfirm: false
	};

	handleChange = event => {
		this.setState({
			[event.target.name]: event.target.value
		});
	};

	handleClickShowPassword = param => {
		this.setState(state => ({ [param]: !state[param] }));
	};

	canBeSubmitted(forgotPassword) {
		const { email, password, passwordConfirm } = this.state;
		if (forgotPassword) {
			return email.length > 0;
		}
		return email.length > 0 && passwordConfirm.length > 0 && password.length > 0;
	}

	validPassword = () => {
		const { password } = this.state;
		var passwordRegex = /^(?=.*[0-9])(?=.*[\^$*.[\]{}()?\-"!@#%&/\\,><':;|_~`+=])(?=.*[a-z])(?=.*[A-Z])([\w^$*.[\]{}()?\-"!@#%&/\\,><':;|_~`+=]{8,16})$/;
		return password.length && !passwordRegex.test(password);
	};

	renderForgotPassword = () => {
		const { email } = this.state;
		const { classes } = this.props;

		return (
			<React.Fragment>
				<Typography className={classes.titleForm} variant='h6'>
					RECOVER YOUR PASSWORD
				</Typography>
				<form name='recoverPasswordForm' className={classes.form} noValidate>
					<TextField
						className={classes.textField}
						label='Email'
						autoFocus
						type='email'
						name='email'
						value={email}
						onChange={this.handleChange}
						variant='outlined'
						required
						fullWidth
					/>

					<Button
						className={classes.buttonSubmit}
						variant='contained'
						color='primary'
						aria-label='SEND RESET LINK'
						disabled={!this.canBeSubmitted(true)}
						onClick={() => this.props.handleLoginUser(this.state.email, this.state.password)}
					>
						SEND RESET LINK
					</Button>
				</form>
			</React.Fragment>
		);
	};
	renderResetPassword = () => {
		const { email, password, passwordConfirm } = this.state;

		return (
			<React.Fragment>
				<Typography variant='h6' color='red'>
					RESET YOUR PASSWORD
				</Typography>
				<form name='loginForm' noValidate>
					<TextField
						label='Email'
						autoFocus
						type='email'
						name='email'
						value={email}
						onChange={this.handleChange}
						variant='outlined'
						required
						fullWidth
					/>

					<TextField
						label='Password'
						type={this.state.showPassword ? 'text' : 'password'}
						name='password'
						value={password}
						onChange={this.handleChange}
						variant='outlined'
						required
						fullWidth
						error={this.validPassword()}
						InputProps={{
							endAdornment: (
								<InputAdornment position='end'>
									<IconButton
										aria-label='Toggle password visibility'
										onClick={() => this.handleClickShowPassword('showPassword')}
									>
										{this.state.showPassword ? <VisibilityOff /> : <Visibility />}
									</IconButton>
								</InputAdornment>
							)
						}}
					/>
					<TextField
						label='Password (Confirm)'
						type={this.state.showPasswordConfirm ? 'text' : 'password'}
						name='passwordConfirm'
						value={passwordConfirm}
						onChange={this.handleChange}
						variant='outlined'
						required
						fullWidth
						error={passwordConfirm.length && passwordConfirm != password}
						InputProps={{
							endAdornment: (
								<InputAdornment position='end'>
									<IconButton
										aria-label='Toggle password visibility'
										onClick={() => this.handleClickShowPassword('showPasswordConfirm')}
									>
										{this.state.showPasswordConfirm ? <VisibilityOff /> : <Visibility />}
									</IconButton>
								</InputAdornment>
							)
						}}
					/>

					<Button
						variant='contained'
						color='primary'
						className='w-full mx-auto mt-16'
						aria-label='LOG IN'
						disabled={!this.canBeSubmitted()}
						onClick={() => this.props.handleLoginUser(this.state.email, this.state.password)}
					>
						RESET MY PASSWORD
					</Button>
				</form>
			</React.Fragment>
		);
	};

	render() {
		const { classes } = this.props;

		return (
			<Grid container className={classes.rootCard}>
				<Grid item xs={12} sm={8} md={4} className={classes.rightPanel}>
					{this.renderForgotPassword()}
					<div style={{ paddingTop: '1rem', paddingBottom: '1rem' }}>
						<Link className={classes.links} to='/login'>
							Go back to login
						</Link>
					</div>
				</Grid>
			</Grid>
		);
	}
}

const mapStateToProps = state => {
	return {
		listaFormularios: state.authReducers
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleLoginUser: (email, password) => {
			dispatch(userActions.LoginUser({ email, password }));
		}
	};
};

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(withStyles(styles, { withTheme: true })(AuthManagePassword));
